.button {
  width: 100%;
  height: 100%;
  background: #62a1eb;
  border-radius: 8px;
  color: white;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: 300ms;
  &:hover{
    transition: 300ms;
    background-color: #488edf;
  }
}

.button:disabled{
  background-color: gray;
  cursor: initial;
}
