@use '../../../variables.scss' as s;

.wrapper{
  position: absolute;
  width: 886px;
  height: 120px;
  left: 174px;
  top: 688px;
  display: flex;
  justify-content: space-between;
  hr{
    width: 100%;
    position: absolute;
    bottom: -70px;
    background-color: #C7C7C7;
  }
}

.laptop_name{
  width: 50%;
}

.line{
  width: 100%;
  height: 1px;
  background-color: gray;
  position: absolute;
  bottom: -70px;
}

@include s.media(390px){
  .wrapper{
    width: 380px;
    left: 13px;
    top: 350px;
    flex-direction: column;

  }
  .laptop_name{
    width: 94%;
  }
  .line{
    display: none;
  }
}

